import React, { Component } from 'react'
import NumberFormat from 'react-number-format';

export default class CompoundInterest extends Component {
    constructor(props) {
        super(props);
        this.state = ({
            StartAmount: 0,
            MonthlyAdded: 10000,
            YearlyInterest: 4.0,
            AlternateInterest: 5.0,
            InterestTax: 22,
            MonthlyCosts: 0,
            AlternateMonthlyCosts: 200,
            CashWithdraralCost: 0.6,
            AlternateCashWithdraralCost: 0.7,
            AlternateInterestTax: 16,

            GridYears: 30,
            CashGrid: '',
            InterestGrid: '',
        });
    }

    componentDidMount() {
        this.calcCosts()
    }

    outDate = () => {
        let output = (
            <div  >

            </div>
        );

        this.props.Sidebar(output);
    }

    changed = (x, y) => {
        this.setState({ [y]: x.floatValue }, () => this.SetMonthsTable());
    }

    calcCosts() {
        let obj = {};
        this.setState(obj, () => this.SetMonthsTable());
    }

    SetMonthsTable = () => {
        let rows = [];
        let yearsBalance = [];
        let altyearsBalance = [];
        let interestgrid = [];
        let yearsToShow = this.state.GridYears;
        let prevbalance = this.state.StartAmount;
        let prevaltbalance = this.state.StartAmount;


        for (let i = 1; i <= yearsToShow * 12; i++) {
            let balance = prevbalance + this.state.MonthlyAdded - this.state.MonthlyCosts + (((i > 1 ? prevbalance : 0) + this.state.MonthlyAdded) * ((this.state.YearlyInterest / 100) * (1 - (this.state.InterestTax / 100)))) / 12;
            prevbalance = balance;
            let altbalance = prevaltbalance + this.state.MonthlyAdded - this.state.AlternateMonthlyCosts + (((i > 1 ? prevaltbalance : 0) + this.state.MonthlyAdded) * ((this.state.AlternateInterest / 100) * (1 - (this.state.AlternateInterestTax / 100)))) / 12;
            prevaltbalance = altbalance;


            if (i % 12 === 0) {
                yearsBalance[i / 12] = Math.round(balance);
                altyearsBalance[i / 12] = Math.round(altbalance);
            }
            rows.push(
                <tr key={i}>
                    <td>{i}</td>
                    <td><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={balance} className="text-right " /></td>
                    <td className=" bg-lightwarn"><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={altbalance} className="text-right " /></td>
                </tr>
            )
        }

        for (let i = 1; i <= yearsToShow; i++) {
            let fullyearinterest = (yearsBalance[i] - this.state.StartAmount - (i * 12 * this.state.MonthlyAdded)) - (yearsBalance[i] * (this.state.CashWithdraralCost / 100));
            let altfullyearinterest = (altyearsBalance[i] - this.state.StartAmount - (i * 12 * this.state.MonthlyAdded)) - (altyearsBalance[i] * (this.state.AlternateCashWithdraralCost / 100));

            interestgrid.push(
                <tr key={i}>
                    <td>{i}. v</td>
                    <td><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={fullyearinterest} className="text-right " /></td>
                    <td><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={fullyearinterest / (12 * i)} className="text-right " /></td>
                    <td className=" bg-lightwarn"><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={altfullyearinterest} className="text-right " /></td>
                    <td className=" bg-lightwarn"><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={altfullyearinterest / (12 * i)} className="text-right " /></td>
                </tr>
            )
        }
        //=(D28-B6-(12*B7))-(D28*(B13))

        this.setState({
            InterestGrid: (<table className=" bg-white table table-sm table-bordered text-right">
                <thead className="bg-own text-center sticky-top">
                    <tr >

                        <th colSpan="5">ves</th>
                    </tr>
                    <tr>
                        <th rowSpan="2">v</th>
                        <th rowSpan="2">ves kamat </th>
                        <th rowSpan="2">Havi kamat</th>
                        <th colSpan="2" className="text-center ">Alternatv </th>
                    </tr>

                    <tr>
                        <th className="  ">ves kamat</th>
                        <th className="  ">havi kamat</th>
                    </tr>
                </thead>
                <tbody>
                    {interestgrid}
                </tbody>
            </table>),
            CashGrid: (<table className=" bg-white table table-sm table-bordered text-right">
                <thead className="bg-own text-center sticky-top">
                    <tr  >
                        <th colSpan="3">Havi</th>
                    </tr>

                    <tr>
                        <th>Hnap</th>
                        <th>Egyenleg</th>
                        <th >Alternatv egyenleg</th>
                    </tr>
                </thead>
                <tbody>
                    {rows}
                </tbody>
            </table>)
        }, () => this.outDate()); 
    }

    render() {
        return (
            <div>
                <div className="jumbotron jumbotron-fluid pt-3 pb-3 mb-3">
                    <div className="container">
                        <h1 className="display-5 text-own">Megtakartsi kalkultor</h1>
                        <p className="lead">Havi kamatfizetssel szmol.
                        A srgval jellt alternatvt nem ktelez kitlteni, csak ha kvncsi vagy, mshol mennyit fialna a pnzed.
                        A pnzfelvtel djval csak a futamid vgn, felvtelkor szmol, az ves sszests tartalmazza ezt a kltsget.
                            <a rel="noopener noreferrer" target="_blank" href="https://kiszamolo.hu/hova-tegyem-a-havi-megtakaritasomat-kalkulator/">  Tovbbi tudnival.</a><br />
                            Fontos figyelmeztets: az sszeg nincs inflcival korriglva!</p>
                    </div>
                </div>


                <div className="card   mb-3 border-own" > 
                        <div className="card-body   ">

                    <div className="row pt-2 pb-2">
                        <div className="col-lg-6">
                            Kezd sszeg:
                            </div> <div className="col-lg-6">
                            <NumberFormat suffix=" Ft" onValueChange={values => this.changed(values, "StartAmount")} value={this.state.StartAmount} className="text-right form-control" thousandSeparator=" " decimalSeparator="," />
                        </div>
                    </div>
                    <div className="row pt-2 pb-2">
                        <div className="col-lg-6">
                            Havi befizets:
                            </div> <div className="col-lg-6">
                            <NumberFormat suffix=" Ft" onValueChange={values => this.changed(values, "MonthlyAdded")} value={this.state.MonthlyAdded} className="text-right form-control" thousandSeparator=" " decimalSeparator="," />
                        </div>
                    </div>
                    </div>

                </div>


                <div className="card   mb-3 border-own  " > 
                <div className="card-body    ">
                    <div className="row pt-2 pb-2">
                        <div className="col-lg-6">
                            ves kamatlb:
                            </div> <div className="col-lg-6">
                            <NumberFormat suffix=" %" onValueChange={values => this.changed(values, "YearlyInterest")} value={this.state.YearlyInterest} className="text-right form-control" thousandSeparator=" " decimalSeparator="," />                           </div>
                    </div>
                    <div className="row pt-2 pb-2">
                        <div className="col-lg-6">
                            Kamatad mrtke:
                            </div> <div className="col-lg-6">
                            <NumberFormat suffix=" %" onValueChange={values => this.changed(values, "InterestTax")} value={this.state.InterestTax} className="text-right form-control" thousandSeparator=" " decimalSeparator="," />                            </div>
                    </div>
                    
                    <div className="row pt-2 pb-2">
                        <div className="col-lg-6">
                            Pnzfelvtel dja:
                            </div> <div className="col-lg-6">
                            <NumberFormat suffix=" %" onValueChange={values => this.changed(values, "CashWithdraralCost")} value={this.state.CashWithdraralCost} className="text-right form-control" thousandSeparator=" " decimalSeparator="," />                           </div>
                    </div>

                    <div className="row pt-2 pb-2">
                        <div className="col-lg-6">
                            Havi szmlakltsgek:
                            </div> <div className="col-lg-6">
                            <NumberFormat suffix=" Ft" onValueChange={values => this.changed(values, "MonthlyCosts")} value={this.state.MonthlyCosts} className="text-right form-control" thousandSeparator=" " decimalSeparator="," />                            </div>
                            </div>
                        </div>

                    </div>


                <div className="card   mb-3 border-own bg-lightwarn" > 
                    <div className="card-body    ">

                    <div className="row pt-2 pb-2    ">
                        <div className="col-lg-6">
                            Alternatv kamat mrtke:
                            </div> <div className="col-lg-6">
                            <NumberFormat suffix=" %" onValueChange={values => this.changed(values, "AlternateInterest")} value={this.state.AlternateInterest} className="text-right form-control" thousandSeparator=" " decimalSeparator="," />                            </div>
                    </div>
                    <div className="row pt-2 pb-2">
                        <div className="col-lg-6">
                        Alternatv kamatad mrtke:
                            </div> <div className="col-lg-6">
                            <NumberFormat suffix=" %" onValueChange={values => this.changed(values, "AlternateInterestTax")} value={this.state.AlternateInterestTax} className="text-right form-control" thousandSeparator=" " decimalSeparator="," />                            </div>
                    </div>
                    
                    <div className="row pt-2 pb-2">
                        <div className="col-lg-6">
                        Alternatv pnzfelvtel dja:
                            </div> <div className="col-lg-6">
                            <NumberFormat suffix=" %" onValueChange={values => this.changed(values, "AlternateCashWithdraralCost")} value={this.state.AlternateCashWithdraralCost} className="text-right form-control" thousandSeparator=" " decimalSeparator="," />                           </div>
                    </div>
                    <div className="row pt-2 pb-2    ">
                        <div className="col-lg-6">
                            Alternatv havi kltsg:
                            </div> <div className="col-lg-6">
                            <NumberFormat suffix=" Ft" onValueChange={values => this.changed(values, "AlternateMonthlyCosts")} value={this.state.AlternateMonthlyCosts} className="text-right form-control" thousandSeparator=" " decimalSeparator="," />                           </div>
                    </div>

                    
                </div>
                </div>

                {this.state.InterestGrid}

                {this.state.CashGrid}
            </div>
        )
    }
}
