import React, { Component } from 'react'
import NumberFormat from 'react-number-format';

export default class ApartmentInvestment extends Component {
    constructor(props) {
        super(props);
        this.state = ({
            ApartmentCost: 14000000,
            RenovationCost: 1100000,

            LawyerCost: 1,
            DutyCost: 4,
            RealEstateAgentCost: 3,

            FullInvestment: 0,

            BankInterest: 4,
            DesiredProfit: 2,
            InterestTax: 0,
            Inflation: 3,
            PropertyPriceChange: 3,
            RentalTax: 16,
            Amortisation: 1.5,
            RecurringRenovationCost: 2000000,
            MaintenanceCost: 50000,
            RentalMonths: 10,
            PropertyTax: 0,
            AdditionalMonthlyCost: 800,

            Investment: 0,
            ApartmentExpectedProfit: 0,
            MonthlyRenovation: 0,
            MaintenanceMonthly: 0,
            AmortisationMontly: 0,
            PropertyTaxMontly: 0,
            ApartmentValueIncrease: 0,
            ExpectedYearlyProfit: 0,
            ProfitBeforeTax: 0,
            RentalPrice: 0,
        });
    }

    componentDidMount() {
        this.calcCosts()
    }

    outDate = () => {
        let output = (
            <div>
            </div>
        );
        this.props.Sidebar(output);
    }

    changed = (x, y) => {
        this.setState({ [y]: x.floatValue }, () => this.calcCosts());
    }

    calcCosts() {
        let obj = {};
        let LawyerCost = (parseFloat(this.state.LawyerCost) / 100) || 0;
        let DutyCost = (parseFloat(this.state.DutyCost) / 100) || 0;
        let RealEstateAgentCost = (parseFloat(this.state.RealEstateAgentCost) / 100) || 0;
        obj.FullInvestment = this.state.ApartmentCost * (1 + (LawyerCost + (DutyCost) + (RealEstateAgentCost))) + this.state.RenovationCost;

        let Interest = (parseFloat(this.state.BankInterest) / 100) || 0;
        let DesiredProfit = (parseFloat(this.state.DesiredProfit) / 100) || 0;
        let InterestTax = (parseFloat(this.state.InterestTax) / 100) || 0;
        let Amortisation = (parseFloat(this.state.Amortisation) / 100) || 0;
        let Inflation = (parseFloat(this.state.Inflation) / 100) || 0;
        let PropertyPriceChange = (parseFloat(this.state.PropertyPriceChange) / 100) || 0;
        let RentalTax = (parseFloat(this.state.RentalTax) / 100) || 0;


        obj.Investment = ((obj.FullInvestment * (1 + Interest)) - obj.FullInvestment) * (1 - InterestTax);
        obj.ApartmentExpectedProfit = ((obj.FullInvestment * (1 + Interest + DesiredProfit)) - obj.FullInvestment) * (1 - InterestTax);
        obj.MonthlyRenovation = this.state.RecurringRenovationCost / 120;
        obj.MaintenanceMonthly = this.state.MaintenanceCost / 12;
        obj.AmortisationMontly = (obj.FullInvestment * Amortisation) / 12;
        obj.PropertyTaxMontly = this.state.PropertyTax / 12;
        obj.ApartmentValueIncrease = (this.state.ApartmentCost * (PropertyPriceChange - Inflation));
        obj.ExpectedYearlyProfit = obj.ApartmentExpectedProfit + (obj.MaintenanceMonthly * 12) + (obj.MonthlyRenovation * 12) + (obj.AmortisationMontly * 12) + (this.state.PropertyTax) + (this.state.AdditionalMonthlyCost * 12) - obj.ApartmentValueIncrease;
        obj.ProfitBeforeTax = obj.ExpectedYearlyProfit / (1 - RentalTax);
        obj.RentalPrice = this.state.RentalMonths > 0 ? obj.ProfitBeforeTax / this.state.RentalMonths : 0;

        this.setState(obj, () => this.outDate());
    }

    render() {
        return (
            <div>
                <div className="jumbotron jumbotron-fluid pt-3 pb-3 mb-3">
                    <div className="container">
                        <h1 className="display-5 text-own">Mennyirt kell kiadnod a laksodat, hogy megrje?</h1>
                        <p className="lead">(Ez egy kalkultor, ami azt jelenti, hogy a sajt szmaidat rd be, ne a bertakon gondolkodjl.)</p>
                    </div>
                </div>

                <div className="">
                    <div className="  row pt-2 pb-2 div-alt">
                        <div className="col-lg-8 value-title">
                            A laks vtelra:
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" v" thousandSeparator=" " decimalSeparator="," onValueChange={(values) => this.changed(values, "ApartmentCost")} value={this.state.ApartmentCost} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt">
                        <div className="col-lg-8 value-title">
                            gyvdi dj:
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "LawyerCost")} value={this.state.LawyerCost} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Vagyonszerzsi illetk:
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "DutyCost")} value={this.state.DutyCost} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Ingatlangynk dja:
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "RealEstateAgentCost")} value={this.state.RealEstateAgentCost} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            A vtel utn rfordtand sszeg (kifests, bebtorzs, mosgp, parkettacsere, stb.):
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "RenovationCost")} value={this.state.RenovationCost} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row  pt-2 pb-3 div-alt ">
                        <div className="col-lg-8 value-title">
                            Teljes beruhzs tkeignye:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.FullInvestment} className="text-right " />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Alternatv befektets inflci feletti hozama (pldul prmium llampapr):
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "BankInterest")} value={this.state.BankInterest} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            A lakskiads kockzatos s dolgozni kell vele. Ennyivel hozzon tbbet, mint a kockzatmentes befektets:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "DesiredProfit")} value={this.state.DesiredProfit} className="text-right form-control" />
                        </div>
                    </div>


                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Kamatad (TBSZ szmlnl 0%):
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "InterestTax")} value={this.state.InterestTax} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Becslt inflci hossz tvon:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "Inflation")} value={this.state.Inflation} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Az ingatlanok rtkemelkedse hossz tvon (inflcival szoktak drgulni.):
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "PropertyPriceChange")} value={this.state.PropertyPriceChange} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Lakskiads utni ad mrtke (vi 1 milli felett EHO is van!):
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "RentalTax")} value={this.state.RentalTax} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            A laks amortizcija vente (egy laks is regszik, ezrt cskken is az rtke. Az jpts tbbet r, mint egy 20 vagy 30 ves laks.):
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "Amortisation")} value={this.state.Amortisation} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            A laks feljtsnak kltsge 10 ves tlagban (ne csak az els 10 vre gondolj. Ksbb ablakcsere, vezetkcsere, j csempe, stb.) Teht 10 vente ennyi:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "RecurringRenovationCost")} value={this.state.RecurringRenovationCost} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            zemeltetsi kltsg vente (elromlott, letrt, eldugult, leszakadt. Ez mind a te kiadsod, mint tulajdonos.)

                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "MaintenanceCost")} value={this.state.MaintenanceCost} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Hny hnapig van kiadva a laks vente? (Msfl vente legalbb egy hnapig resen ll, az gynk is elkr egy havi djat. rdemes vi 10 hnappal szmolni):
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" hnap" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "RentalMonths")} value={this.state.RentalMonths} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Ingatlanad mrtke vente:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "PropertyTax")} value={this.state.PropertyTax} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            A brls miatti havi kiads (elmsz beszedni a pnzt, befizeted a csekkeket, stb.):
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "AdditionalMonthlyCost")} value={this.state.AdditionalMonthlyCost} className="text-right form-control" />
                        </div>
                    </div>



                    <div className="card mt-5  border-own">
                
                <div className="card-body pt-2 ">

                    
                <div className="  row   pt-2 pb-3  ">
                        <div className="col-lg-8">
                            A pnzed ennyit hozna rtkpaprban az inflci felett:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.Investment} className="text-right " />
                        </div>
                    </div>

                    <div className="  row  border-top pt-2 pb-3  ">
                        <div className="col-lg-8">
                            A lakstl elvrt hozamod a kltsgek levonsa eltt, inflci felett:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.ApartmentExpectedProfit} className="text-right " />
                        </div>
                    </div>

                    <div className="  row  border-top pt-2 pb-3  ">
                        <div className="col-lg-8">
                            Feljtsi kltsg visszaosztva havi szintre:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.MonthlyRenovation} className="text-right " />
                        </div>
                    </div>

                    <div className="  row  border-top pt-2 pb-3  ">
                        <div className="col-lg-8">
                            zemeltetsi kltsg havonta:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.MaintenanceMonthly} className="text-right " />
                        </div>
                    </div>

                    <div className="  row  border-top pt-2 pb-3  ">
                        <div className="col-lg-8">
                            A laks amortizcija havonta:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.AmortisationMontly} className="text-right " />
                        </div>
                    </div>

                    <div className="  row  border-top pt-2 pb-3  ">
                        <div className="col-lg-8">
                            Ingatlanad havonta:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.PropertyTaxMontly} className="text-right " />
                        </div>
                    </div>

                    <div className="  row  border-top pt-2 pb-3  ">
                        <div className="col-lg-8">
                            Az ingatlan inflci feletti rtknvekedse vente:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.ApartmentValueIncrease} className="text-right " />
                        </div>
                    </div>

                    <div className="  row  border-top pt-2 pb-3  ">
                        <div className="col-lg-8">
                            Az ltalad elvrt adzott ves hozam a kltsgeket is figyelembe vve:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.ExpectedYearlyProfit} className="text-right " />
                        </div>
                    </div>
                    <div className="  row  border-top pt-2 pb-3  ">
                        <div className="col-lg-8">
                            Ez adzs eltt ennyi lenne:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.ProfitBeforeTax} className="text-right " />
                        </div>
                    </div>
                    <div className="  row  border-top pt-2 pb-3  ">
                        <div className="col-lg-8">
                            Ennyi hnapot van kiadva a laksod:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" hnap" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.RentalMonths} className="text-right " />
                        </div>
                    </div>
                    <div className="  row  border-top pt-2 pb-3  ">
                        <div className="col-lg-8">
                            Teht ennyirt kell kiadnod havonta:

                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="" displayType="text" decimalScale="0" value={this.state.RentalPrice} className="text-right h3 " />
                        </div>
                    </div>
                </div>
            </div>



                </div>


            </div>
        )
    }
}
