import React, { Component } from 'react'
import NumberFormat from 'react-number-format';

export default class ApartmentForStudents extends Component {
    constructor(props) {
        super(props);
        this.state = ({
            ApartmentCost: 18000000,
            LawyerCost: 1,
            DutyCost: 4,
            RealEstateAgentCost: 4,
            RenovationCost: 400000,
            FurnitureCost: 400000,

            FullInvestment: 0,

            BankInterest: 5,
            MissedBankInerests: 0,
            UniversityLength: 5.5,
            RentalCost: 95000,
            RentalCostSum: 0,
            RentalMonths: 11,
            SharedRentalCost: 55000,
            SharedRentalCostSum: 0,
            SharedRentalMonths: 11,

            DormCost: 25000,
            DormCostSum: 0,
            DormMonths: 10,

            ApartmentPricesIncrease: 25,
            ApartmentProfit: 0,
            RentalCostSellPrice: 0,
            SharedRentalCostSellPrice: 0,
            DormCostSellPrice: 0,


            ApartmentPricesDecrease: -20,
            ApartmentLoss: 0,
            DecreaseRentalCostSellPrice: 0,
            DecreaseSharedRentalCostSellPrice: 0,
            DecreaseDormCostSellPrice: 0,

        });
    }

    componentDidMount() {
        this.calcCosts()
    }

    outDate = () => {
        let output = (
            <div>
            </div>
        );

        this.props.Sidebar(output);
    }

    changed = (x, y) => {
        this.setState({ [y]: x.floatValue }, () => this.calcCosts());
    }

    calcCosts() {
        let obj = {};
        let LawyerCost = (parseFloat(this.state.LawyerCost) / 100) || 0;
        let DutyCost = (parseFloat(this.state.DutyCost) / 100) || 0;
        let RealEstateAgentCost = (parseFloat(this.state.RealEstateAgentCost) / 100) || 0;
        obj.FullInvestment = this.state.ApartmentCost * (1 + (LawyerCost + (DutyCost) + (RealEstateAgentCost))) + this.state.RenovationCost + this.state.FurnitureCost;


        obj.MissedBankInerests = (obj.FullInvestment * (Math.pow((1 + (this.state.BankInterest / 100)), this.state.UniversityLength))) - obj.FullInvestment;
        obj.RentalCostSum = this.state.RentalCost * this.state.UniversityLength * this.state.RentalMonths;
        obj.SharedRentalCostSum = this.state.SharedRentalCost * this.state.UniversityLength * this.state.SharedRentalMonths;
        obj.DormCostSum = this.state.DormCost * this.state.UniversityLength * this.state.DormMonths;
        obj.ApartmentProfit = this.state.ApartmentCost * (1 + (this.state.ApartmentPricesIncrease / 100)) - obj.FullInvestment;
        obj.RentalCostSellPrice = obj.RentalCostSum - obj.MissedBankInerests + obj.ApartmentProfit;
        obj.SharedRentalCostSellPrice = obj.SharedRentalCostSum - obj.MissedBankInerests + obj.ApartmentProfit;
        obj.DormCostSellPrice = obj.DormCostSum - obj.MissedBankInerests + obj.ApartmentProfit;
        obj.ApartmentLoss = this.state.ApartmentCost * (1 + (this.state.ApartmentPricesDecrease / 100)) - obj.FullInvestment;
        obj.DecreaseRentalCostSellPrice = obj.RentalCostSum - obj.MissedBankInerests + obj.ApartmentLoss;
        obj.DecreaseSharedRentalCostSellPrice = obj.SharedRentalCostSum - obj.MissedBankInerests + obj.ApartmentLoss;
        obj.DecreaseDormCostSellPrice = obj.DormCostSum - obj.MissedBankInerests + obj.ApartmentLoss;

        this.setState(obj, () => this.outDate());
    }

    render() {
        return (
            <div>
                <div className="jumbotron jumbotron-fluid pt-3 pb-3 mb-3">
                    <div className="container">
                        <h1 className="display-5 text-own">Laksvsrls vagy brls a dikoknak? </h1>
                        <p className="lead">(Ez egy kalkultor, ami azt jelenti, hogy a sajt szmaidat rd be, ne a bertakon gondolkodjl.)</p>
                    </div>
                </div>

                <div className="">
                    <div className="  row pt-2 pb-2 div-alt">
                        <div className="col-lg-8 value-title">
                            A laks vtelra:
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" v" thousandSeparator=" " decimalSeparator="," onValueChange={(values) => this.changed(values, "ApartmentCost")} value={this.state.ApartmentCost} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt">
                        <div className="col-lg-8 value-title">
                            gyvdi dj:
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "LawyerCost")} value={this.state.LawyerCost} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Illetk:
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "DutyCost")} value={this.state.DutyCost} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Ingatlanos jutalka (vagy vtelnl, vagy eladsnl fizeted):
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "RealEstateAgentCost")} value={this.state.RealEstateAgentCost} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Feljtsra klttt pnz:
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "RenovationCost")} value={this.state.RenovationCost} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Berendezsre klttt pnz:
                  </div> <div className="col-lg-4">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "FurnitureCost")} value={this.state.FurnitureCost} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row pt-2 pb-3 div-alt ">
                        <div className="col-lg-8 value-title">
                            Befektets sszesen:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.FullInvestment} className="text-right " />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Banki kamat (prmium llampapr inflci plusz 1,4%):
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "BankInterest")} value={this.state.BankInterest} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Elmaradt banki kamatok mrtke:
                  </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," decimalScale="0" displayType="text" value={this.state.MissedBankInerests} className="text-right" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2   bg-lightgreen">
                        <div className="col-lg-8 value-title">
                            Legjobb esetben a laksrak ennyivel nnek az eladsig:
             </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "ApartmentPricesIncrease")} value={this.state.ApartmentPricesIncrease} className="text-right form-control" />
                        </div>
                    </div>
                    <div className="  row pt-2 pb-3   bg-lightgreen ">
                        <div className="col-lg-8 value-title">
                            Lakson elrt haszon (eladsi r-mnusz sszes rfordts):
             </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.ApartmentProfit} className="text-right  " />
                        </div>
                    </div>


                    <div className="  row pt-2 pb-2    bg-lightred ">
                        <div className="col-lg-8 value-title">
                            Legrosszabb esetben ennyivel cskkennek az rak az eladsig:
             </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "ApartmentPricesDecrease")} value={this.state.ApartmentPricesDecrease} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-3  bg-lightred ">
                        <div className="col-lg-8 value-title">
                            Lakson elrt vesztesg (eladsi r-mnusz sszes rfordts):
             </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.ApartmentLoss} className="text-right  " />
                        </div>
                    </div>



                    <div className="  row pt-2 pb-2 div-alt ">
                        <div className="col-lg-8 value-title">
                            Egyetemi vek szma:
             </div> <div className="col-lg-4 text-right">
                            <NumberFormat suffix=" v" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "UniversityLength")} value={this.state.UniversityLength} className="text-right form-control" />
                        </div>
                    </div>



                    <table className=" bg-white table table-sm table-bordered text-right">
                        <thead>
                            <tr className="bg-own text-center" >
                                <th></th>
                                <th>Egyedl brls</th>
                                <th>Brls mssal kzsen</th>
                                <th>Kollgium</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>Brleti dj havonta:</td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "RentalCost")} value={this.state.RentalCost} className="text-right form-control" /></td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "SharedRentalCost")} value={this.state.SharedRentalCost} className="text-right form-control" /></td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "DormCost")} value={this.state.DormCost} className="text-right form-control" /></td>
                            </tr>

                            <tr>
                                <td>Brleti dj sszesen:</td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.RentalCostSum} className="text-right " /></td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.SharedRentalCostSum} className="text-right " /></td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.DormCostSum} className="text-right " /></td>
                            </tr>


                            <tr>
                                <td>Hny hnap egy vben?</td>
                                <td> <NumberFormat suffix=" hnap" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "RentalMonths")} value={this.state.RentalMonths} className="text-right form-control" /></td>
                                <td> <NumberFormat suffix=" hnap" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "SharedRentalMonths")} value={this.state.SharedRentalMonths} className="text-right form-control" /></td>
                                <td> <NumberFormat suffix=" hnap" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "DormMonths")} value={this.state.DormMonths} className="text-right form-control" /></td>
                            </tr>

                            <tr >
                                <td colSpan="4" className="pb-4">(Megjegyzs: a laksbrlsnl van rezsi is, de az a sajt laksnl is van.) </td>
                            </tr>

                            <tr className="text-center bg-lightgreen">
                                <td colSpan="4">Nvekv laksrak melletti brleti dj mnusz elmaradt kamatok plusz lakselads haszna:</td>
                            </tr>

                            <tr className="  bg-lightgreen">
                                <td></td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.RentalCostSellPrice} className="text-right " /></td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.SharedRentalCostSellPrice} className="text-right " /></td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.DormCostSellPrice} className="text-right " /></td>
                            </tr>
                            <tr >
                                <td colSpan="4" className="pb-1">  </td>
                            </tr>
                            <tr className="text-center bg-lightred">
                                <td colSpan="4">Cskken laksrak melletti brleti dj mnusz elmaradt kamatok plusz lakselads vesztesge:</td>
                            </tr>

                            <tr className="  bg-lightred">
                                <td ></td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.DecreaseRentalCostSellPrice} className="text-right   " /></td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.DecreaseSharedRentalCostSellPrice} className="text-right " /></td>
                                <td> <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" decimalScale="0" value={this.state.DecreaseDormCostSellPrice} className="text-right " /></td>
                            </tr>


                        </tbody>
                    </table>

                </div>


            </div>
        )
    }
}
