function    pad(number) {
    var r = String(number);
    if ( r.length === 1 ) {
      r = '0' + r;
    }
    return r;
}   

function HUDateFormat(date){
    return date.getUTCFullYear() + '.' + pad( date.getUTCMonth() + 1 ) + '.' + pad( date.getUTCDate() )
}

export default HUDateFormat;