import React, { Component } from 'react'
import NumberFormat from 'react-number-format';
import PMT from './Helpers/Pmt';
import PPMT from './Helpers/Ppmt';
import FV from './Helpers/Fv';
import CUMIPMT from './Helpers/CumiPmt';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";
import HuDateFormat from './Helpers/DateFormat';

function monthDiff(d1, d2) {
    var months;
    months = (d2.getFullYear() - d1.getFullYear()) * 12;
    months -= d1.getMonth();
    months += d2.getMonth();
    return months <= 0 ? 0 : months;
}



export default class Hitel extends Component {
    constructor(props) {
        super(props);
        this.state = ({
            Loan1SumAmount: 14000000,
            Loan1YearlyInterest: 5.4,
            Loan1Term: 20,
            Loan1StartDate: new Date(2015, 6, 14),
            Loan1PassedMonths: 0,
            Loan1MonthlyCost: 0,
            Loan1FullCost: 0,
            Loan1FullInterests: 0,
            Loan1RemainingMonths: 0,
            Loan1RemainingCost: 0,

            Loan2SumAmount: 11929710,
            Loan2YearlyInterest: 3.3,
            Loan2Term: 11,
            Loan2TermHelper: 0,
            Loan2ReplaceCost: 300000,
            Loan2MonthlyCost: 0,
            Loan2FullCost: 0,
            Loan2FullInterests: 0,
            Loan2BetterCost: 0,

            LoanGrid: '',
        });
    }

    componentDidMount() {
        this.calcCosts()
    }

    outDate = () => {
        let output = (
            <div> 
                <div className="card   mb-3 border-own" >
                    <div className="card-body   ">

                        <div className="pb-2 pt-2 row ">
                            <div className="col-md-7">Rgi hitelbl htralv hnapok szma:</div>
                            <div className="col-md-5 text-right">
                                <NumberFormat suffix=" hnap" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan1RemainingMonths} />
                            </div>
                        </div>

                        <div className="pb-2 pt-2 row  border-top">
                            <div className="col-md-7">Havonta fizetend:</div>
                            <div className="col-md-5 text-right">
                                <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan1MonthlyCost} />
                            </div>
                        </div>

                        <div className="pb-2 pt-2 row  border-top">
                            <div className="col-md-7">Mg fizetend:</div>
                            <div className="col-md-5 text-right">
                                <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan1RemainingCost} />
                            </div>
                        </div>

                        <div className="pb-2 pt-2   border-top">
                            <div className="">Hitelkivlts nyeresge a kltsgeket is figyelembe vve:</div>
                            <div className=" text-right">
                                <h3><NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan2BetterCost} /></h3>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );

        this.props.Sidebar(output);
    }

    changed = (x, y) => {
        this.setState({ [y]: x }, () => this.calcCosts());
    }

    SetLoan2Term() {

        let obj = {};
        obj.Loan1PassedMonths = monthDiff(this.state.Loan1StartDate, new Date());
        obj.Loan2Term = Math.ceil(((this.state.Loan1Term * 12) - obj.Loan1PassedMonths) / 12);
        this.setState(obj, () => { this.SetMonthsTable() });
    }

    calcCosts() {

        let obj = {};
        obj.Loan1PassedMonths = monthDiff(this.state.Loan1StartDate, new Date());
        obj.Loan1MonthlyCost = Math.round(-1 * (PMT((this.state.Loan1YearlyInterest / 100) / 12, this.state.Loan1Term * 12, this.state.Loan1SumAmount)));
        obj.Loan1FullInterests = Math.round(-1 * (CUMIPMT(this.state.Loan1YearlyInterest / 100 / 12, this.state.Loan1Term * 12, this.state.Loan1SumAmount, 1, this.state.Loan1Term * 12, 0)));
        obj.Loan1FullCost = obj.Loan1FullInterests + this.state.Loan1SumAmount;
        obj.Loan2Term = this.state.Loan2Term;
        obj.Loan1RemainingMonths = (this.state.Loan1Term * 12) - obj.Loan1PassedMonths;
        obj.Loan2PassedMonths = monthDiff(this.state.Loan1StartDate, new Date());
        obj.Loan2MonthlyCost = Math.round(-1 * (PMT((this.state.Loan2YearlyInterest / 100) / 12, obj.Loan2Term * 12, this.state.Loan2ReplaceCost + this.state.Loan2SumAmount)));
        obj.Loan2FullInterests = Math.round(-1 * (CUMIPMT(this.state.Loan2YearlyInterest / 100 / 12, obj.Loan2Term * 12, this.state.Loan2ReplaceCost + this.state.Loan2SumAmount, 1, this.state.Loan2Term * 12, 0)));
        obj.Loan2FullCost = this.state.Loan2ReplaceCost + this.state.Loan2SumAmount + obj.Loan2FullInterests;
        obj.Loan1RemainingCost = obj.Loan1RemainingMonths * obj.Loan1MonthlyCost;
        obj.Loan2BetterCost = obj.Loan1RemainingCost - obj.Loan2FullCost;
        this.setState(obj, () => { this.SetMonthsTable() });
    }


    SetMonthsTable = () => {
        let rows = [];

        for (let i = 1; i <= this.state.Loan1Term * 12; i++) {
            let d = new Date(this.state.Loan1StartDate.getTime());
            let payTime = new Date(d.setMonth(d.getMonth() + i))
            let cash = Math.abs(FV(this.state.Loan1YearlyInterest / 100 / 12, i - 1, -this.state.Loan1MonthlyCost, this.state.Loan1SumAmount))
            let cash2 = Math.abs(PPMT(this.state.Loan1YearlyInterest / 100 / 12, i, this.state.Loan1Term * 12, this.state.Loan1SumAmount))
            let interest = Math.abs(this.state.Loan1MonthlyCost - cash2);
            let remaining = Math.abs(cash - cash2);

            rows.push(
                <tr key={i}>
                    <td>{i}</td>
                    <td>{HuDateFormat(payTime)}</td>
                    <td><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={cash} className="text-right " /></td>
                    <td><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={cash2} className="text-right " /></td>
                    <td><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={interest} className="text-right " /></td>
                    <td><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={remaining} className="text-right " /></td>

                </tr>
            )
        }

        this.setState({
            LoanGrid: (<table className=" bg-white table table-sm table-bordered text-right ">
                <thead className="sticky-top bg-own">
                    <tr>
                        <th rowSpan="2">Hnap</th>
                        <th rowSpan="2">Fizets dtuma</th>
                        <th rowSpan="2">Egyenleg a hnap elejn</th>
                        <th colSpan="2" className="text-center">Havi befizets</th>
                        <th rowSpan="2">Fenll tartozs</th>
                    </tr>
                    <tr>
                        <th>Tke rsz</th>
                        <th>Kamat rsz</th>
                    </tr>
                </thead>
                <tbody>
                    {rows}
                </tbody>
            </table>)
        }, () => this.outDate());
    }

    render() {
        return (
            <div>
                <div className="jumbotron jumbotron-fluid pt-3 pb-3 mb-3">
                    <div className="container">
                        <h1 className="display-5 text-own">Hitelkalkultor s hitelkivlts-kalkultor</h1>
                        <p className="lead">
                            Hitelfelvtel eltt rdemes prblgatni, mennyi lenne a fizetend hitel havi trlesztje ms s ms futamidvel. Jelenleg rdemes 3,0-4,4% kamattal szmolni lakshitelt, ehhez nzni ms s ms futamidt. Ha hitelt szeretnl felvenni, krj ingyenes segtsget a blog hitelgyintzjtl, <a href="http://www.olcsohitelek.com" target="_blank" rel="noopener noreferrer" > Gbortl.</a>
                        <br />

gy llitsd be a havi trlesztst, hogy szksg esetn a havi rszlet dupljt is ki tudd fizetni s csak akkor vegyl fel hitelt, ha mellette legalbb hathavi vsztartalkod marad.   <br />

Hitelkivlts esetn add meg a rgi hitel s az j hitel adatait (a rginl a hitelfelvtel idpontjt is) s a hitelkivlts kltsgeit is, megtudod, megri-e kivltani a rgi hitelt. Fontos, ha vltoz kamatozs hitelt vltasz fix kaamtozsra, akkor is megri a biztonsg miatt, ha kicsit emelkedik a trlesztrszlet.
<br />
A tblzatban azt is ltod, mennyi a havi trleszts tke s kamatrsze, illetve mennyi hitel van mg htra.

</p>
                    </div>
                </div>


                <div>
                    <div className="card   mb-3 border-own" >
                        <div className="card-header bg-own">Rgi hitel</div>
                        <div className="card-body   ">


                            <div className="pb-2 row" >
                                <div className="col-md-6 value-title">Hitel sszege</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values.floatValue, "Loan1SumAmount")} value={this.state.Loan1SumAmount} className="text-right form-control" />
                                </div>
                            </div>

                            <div className="pb-2 row">
                                <div className="col-md-6 value-title" >ves kamatlb mrtke</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values.floatValue, "Loan1YearlyInterest")} value={this.state.Loan1YearlyInterest} className="text-right form-control" />
                                </div>
                            </div>
                            <div className="pb-2 row">
                                <div className="col-md-6 value-title">A hitel futamideje vekben</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" v" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values.floatValue, "Loan1Term")} value={this.state.Loan1Term} className="text-right form-control" />
                                </div>
                            </div>
                            <div className="pb-2 row">
                                <div className="col-md-6 value-title">A hitel kezd dtuma</div>
                                <div className="col-md-6 text-right">
                                    <DatePicker locale="hu-HU" popperPlacement="bottom-end" dateFormat="yyyy.MM.dd" showMonthDropdown showYearDropdown selected={this.state.Loan1StartDate} className="text-right form-control" onChange={date => { this.changed(date, "Loan1StartDate"); this.SetLoan2Term() }} />

                                </div>
                            </div>

                            <div className="pb-2 pt-2 row">
                                <div className="col-md-6 ">Havonta fizetend</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " displayType="text" value={this.state.Loan1MonthlyCost} className="text-right " />
                                </div>
                            </div>
                            <div className="pb-2 row border-top">
                                <div className="col-md-6">Hnapok szma</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" hnap" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan1Term * 12} className="text-right " />
                                </div>
                            </div>
                            <div className="pb-2 row  border-top">
                                <div className="col-md-6">sszes kamat a futamid alatt</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan1FullInterests} className="text-right " />
                                </div>
                            </div>
                            <div className="pb-2 row  border-top">
                                <div className="col-md-6">Teljes kifizets sszege</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan1FullCost} className="text-right " />
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
                <div>
                    <div className="card   mb-3 border-own" >
                        <div className="card-header bg-own">j hitel</div>
                        <div className="card-body   ">


                            <div className="pb-2 row" >
                                <div className="col-md-6 value-title">Hitel sszege</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values.floatValue, "Loan2SumAmount")} value={this.state.Loan2SumAmount} className="text-right form-control" />
                                </div>
                            </div>

                            <div className="pb-2 row">
                                <div className="col-md-6 value-title">ves kamatlb mrtke</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values.floatValue, "Loan2YearlyInterest")} value={this.state.Loan2YearlyInterest} className="text-right form-control" />
                                </div>
                            </div>
                            <div className="pb-2 row">
                                <div className="col-md-6 value-title">A hitel futamideje vekben</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" v" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values.floatValue, "Loan2Term")} value={this.state.Loan2Term} className="text-right form-control loan2term" />
                                </div>
                            </div>

                            <div className="pb-4 row">
                                <div className="text-muted col-md-12">
                                    rdemes gy belltani, hogy a havi trleszt ne cskkenjen, inkbb legyen kevesebb a futamid. Errl cikk <a className="text-muted underline" rel="noopener noreferrer" href="http://kiszamolo.hu/valtsd-ki-a-meglevo-ingatlanhiteledet/" target="_blank">itt</a>
                                </div>
                            </div>

                            <div className="pb-2 row">
                                <div className="col-md-6 value-title">Kivlts kltsge:</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values.floatValue, "Loan2ReplaceCost")} value={this.state.Loan2ReplaceCost} className="text-right form-control" />
                                </div>
                            </div>
                            <div className="pb-4 row">
                                <div className="text-muted text-right col-md-12">
                                    Kivltssal jr kltsgek, hozzaddik a hitelhez
                            </div>
                            </div>


                            <div className="pb-2 pt-2 row">
                                <div className="col-md-6">Havonta fizetend</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " displayType="text" value={this.state.Loan2MonthlyCost} className="text-right " />
                                </div>
                            </div>

                            <div className="pb-2 row  border-top">
                                <div className="col-md-6">sszes kamat a futamid alatt</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan2FullInterests} className="text-right " />
                                </div>
                            </div>
                            <div className="pb-2 row  border-top">
                                <div className="col-md-6">Teljes kifizets sszege</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan2FullCost} className="text-right " />
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
                <div>
                    <div className="card   mb-3 border-own" >
                        <div className="card-body   ">

                            <div className="pb-2 pt-2 row ">
                                <div className="col-md-6">Rgi hitelbl htralv hnapok szma:</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" hnap" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan1RemainingMonths} />
                                </div>
                            </div>

                            <div className="pb-2 pt-2 row  border-top">
                                <div className="col-md-6">Havonta fizetend:</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan1MonthlyCost} />
                                </div>
                            </div>

                            <div className="pb-2 pt-2 row  border-top">
                                <div className="col-md-6">Mg fizetend:</div>
                                <div className="col-md-6 text-right">
                                    <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan1RemainingCost} />
                                </div>
                            </div>

                            <div className="pb-2 pt-2   border-top">
                                <div className="">Hitelkivlts nyeresge a kltsgeket is figyelembe vve:</div>
                                <div className=" text-right">
                                    <h3><NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," displayType="text" value={this.state.Loan2BetterCost} /></h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {this.state.LoanGrid}

            </div >
        )
    }
}
