import React, { Component } from 'react'
import NumberFormat from 'react-number-format';
import PMT from './Helpers/Pmt';

export default class Pension extends Component {
    constructor(props) {
        super(props);
        this.state = ({
            HusbandAge: 34,
            WifeAge: 33,
            PensionAgeThreshold: 65,

            MonthlyPension: 300000,
            AlreadyCollectedSum: 500000,

            Interest: 5,
            InterestTax: 16,
            Inflation: 4,

            SavingsGrid: '',
            RemaningYears: 0
        });
    }

    componentDidMount() {
        this.calcCosts()
    }

    outDate = () => {
        let output = (
           <div></div>
        );

        this.props.Sidebar(output);
    }

    changed = (x, y) => {
        this.setState({ [y]: x.floatValue }, () => this.calcCosts());
    }

    calcCosts() {
        let obj ={}; 
        let husbRemain = this.state.PensionAgeThreshold - this.state.HusbandAge ;
        let wifeRemain = this.state.PensionAgeThreshold - this.state.WifeAge ;
        obj.RemaningYears = ((husbRemain + wifeRemain)  / 2);
        
        let c10 = (this.state.Interest/100)*(1- (this.state.InterestTax/100)) * 100;
        let c12 =   ((((1+(c10/100))/(1+(this.state.Inflation/100)))-1))  ;
 
        let d2 = 14.3+(65-this.state.PensionAgeThreshold)+(husbRemain*0.15);
        let d3 = 18.3+(65-this.state.PensionAgeThreshold)+(wifeRemain*0.15);
        let d4 = (d2+d3)/2;
       
        let c8 = (d4*this.state.MonthlyPension*12)-this.state.AlreadyCollectedSum; 
        obj.MonthlySave = -1 * (PMT(c12/12, obj.RemaningYears*12, 1, c8,0));
        
        this.setState(obj, () => this.SetMonthsTable());
    }

    
    SetMonthsTable = () => {
        let rows = [];
        let lastSave = this.state.MonthlySave;
        for (let i = 1; i <= Math.ceil(this.state.RemaningYears); i++) {            
            rows.push(
                <tr key={i}>
                    <td>{i}. v</td>  
                    <td><NumberFormat suffix=" Ft" thousandSeparator=" " decimalScale="0" decimalSeparator="" displayType="text" value={lastSave} className="text-right " /></td>
                </tr>
            )
            lastSave = lastSave * ( 1 + this.state.Inflation/100);
        }

        this.setState({
            SavingsGrid: (<table className=" bg-white table table-sm table-bordered text-right">
                <thead className="bg-own text-center sticky-top"> 
                    <tr >
                        <th colSpan="2">Havonta megtakartaktand sszeg (Inflci miatt nvekszik)</th>
                        </tr>
                    <tr className="bg-own">
                        <th>v</th>
                        <th>sszeg</th>
                      
                    </tr> 
                </thead>
                <tbody>
                    {rows}
                </tbody>
            </table>)
        }, () => this.outDate());
    }

    render() {
        return (
            <div>
                <div className="jumbotron jumbotron-fluid pt-3 pb-3 mb-3">
                    <div className="container">
                        <h1 className="display-5 text-own">Nyugdj kalkultor</h1>
                        <p className="lead">rd be a frj s felesg letkort. A kalkultor 65 ves nyugdj-korhatrral szmol, csak hzasprral mkdik. A vgeredmny csak kzelt rtk s statisztikai tlagon alapul. Olvasd el hozz a cikket <a href="https://kiszamolo.hu/nyugdijkalkulator-ennyit-kell-eltenned-havonta/" target="_blank" rel=" noreferrer noopener">itt</a></p>
                    </div>
                </div>


                <div className="card   mb-3 border-own" > 
                        <div className="card-body   ">

                    <div className="  row pt-2 pb-2 ">
                        <div className="col-lg-8 value-title">
                        Frj jelenlegi letkora vben:
                        </div> 
                        <div className="col-lg-4">
                            <NumberFormat suffix=" v" thousandSeparator=" " decimalSeparator="," onValueChange={(values) => this.changed(values, "HusbandAge")} value={this.state.HusbandAge} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2 ">
                        <div className="col-lg-8 value-title">
                        Felesg jelenlegi letkora vben:
                        </div>
                        <div className="col-lg-4">
                            <NumberFormat suffix=" v" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "WifeAge")} value={this.state.WifeAge} className="text-right form-control" />
                        </div>
                    </div> 
 
                    <div className="  row pt-2 pb-2  ">
                        <div className="col-lg-8 value-title">
                        Nyugdj-korhatr vben:
                        </div>
                        <div className="col-lg-4">
                            <NumberFormat suffix=" v" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "PensionAgeThreshold")} value={this.state.PensionAgeThreshold} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2  ">
                        <div className="col-lg-8 value-title">
                        Mennyibl szeretntek meglni mai rakon az llami nyugdj felett?
                        </div>
                        <div className="col-lg-4">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "MonthlyPension")} value={this.state.MonthlyPension} className="text-right form-control" />
                        </div>
                    </div>
  
                    <div className="  row pt-2 pb-2  ">
                        <div className="col-lg-8 value-title">
                        Nyugdjclra eddig flretett sszeg, vagy vrhat rksg (csak a nyugdjcl megtakartsra sznt rsze!!!!):
                        </div>
                        <div className="col-lg-4">
                            <NumberFormat suffix=" Ft" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "AlreadyCollectedSum")} value={this.state.AlreadyCollectedSum} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2  ">
                        <div className="col-lg-8 value-title">
                        Relisan elrhet brutt kamat a megtakartsodra:
                        </div>
                        <div className="col-lg-4">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "Interest")} value={this.state.Interest} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2  ">
                        <div className="col-lg-8 value-title">
                        Kamatad mrtke:
                        </div>
                        <div className="col-lg-4">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "InterestTax")} value={this.state.InterestTax} className="text-right form-control" />
                        </div>
                    </div>

                    <div className="  row pt-2 pb-2  ">
                        <div className="col-lg-8 value-title">
                        Vrhat inflci mrtke:
                        </div>
                        <div className="col-lg-4">
                            <NumberFormat suffix=" %" thousandSeparator=" " decimalSeparator="," onValueChange={values => this.changed(values, "Inflation")} value={this.state.Inflation} className="text-right form-control" />
                        </div>
                    </div>
 
                </div>
                </div>
                    {this.state.SavingsGrid}
            </div>
        )
    }
}
