import FV from './Fv';
import PMT from './Pmt';

let CUMPRINC = function (rate, periods, value, start, end, type) {
    // Credits: algorithm inspired by Apache OpenOffice
    // Credits: Hannes Stiebitzhofer for the translations of function and variable names
  

    // Return error if either rate, periods, or value are lower than or equal to zero
    if (rate <= 0 || periods <= 0 || value <= 0) {
      return '#NUM!';
    }

    // Return error if start < 1, end < 1, or start > end
    if (start < 1 || end < 1 || start > end) {
      return '#NUM!';
    }

    // Return error if type is neither 0 nor 1
    if (type !== 0 && type !== 1) {
      return '#NUM!';
    }

    // Compute cumulative principal
    var payment = PMT(rate, periods, value, 0, type);
    var principal = 0;
    if (start === 1) {
      if (type === 0) {
        principal = payment + value * rate;
      } else {
        principal = payment;
      }
      start++;
    }
    for (var i = start; i <= end; i++) {
      if (type > 0) {
        principal += payment - (FV(rate, i - 2, payment, value, 1) - payment) * rate;
      } else {
        principal += payment - FV(rate, i - 1, payment, value, 0) * rate;
      }
    }

    // Return cumulative principal
    return principal;
  };


  export default CUMPRINC;